.joe__article--top {
  background-color: rgba($white, .9);
  background: linear-gradient(rgba($white, .75) 0%, $white 100%);
  position: relative;
  padding-top: 1em;

  // create shadow corner effect
  &:before {
    content: "";
    position: absolute;
    top: -6px;
    right: -6px;
    left: -6px;
    z-index: -1;
    display: block;
    background: url('../images/background-gradient.png');
    background-size: 100% 100%;
    background-position: top;
    background-repeat: no-repeat;
    min-height: 450px;
    max-height: 600px;
  }

  @include breakpoint($bp-med) {
    padding-top: 1.5em;
  }
}

.joe__article {
  padding-top: 1px;
}

.joe__article--bottom {
  @include gutter($margin-bottom-full...);
  background-color: $white;
}

.available__in {
  display: flex;
  align-items: center;
  .joe__tags-title {
    padding-top: 0.3em;
    letter-spacing: 0;
  }
  .joe__tags-list li {
    border-bottom: 0;

    > a {
      font-size: 0.95em;
    }
  }
}
